<template>
  <div class="container">
    <div class="toolbar">
      <div class="toolbar-left">
        <h3 class="cell">
          字符串转JSON对象
        </h3>
        <span class="cell">(左侧输入json字符串，点击复制按钮)</span>
      </div>
      <div class="cell toolbar-right">
        <button @click="copy(getFormatJson())">
          复制
        </button>
      </div>
    </div>
    
    <div class="table">
      <div class="row">
        <div class="left">
          <textarea
            v-model="input"
            class="textarea"
            ref="textarea"
          />
        </div>
        <div class="middle">
          <span>>></span>
        </div>
        <div class="right">
          <vue-json-pretty
            :data="json"
            show-length
            :show-double-quotes="false"
            show-select-controller
            virtual
            :virtual-lines="+5000"
            style="height: 100%"
            ref="wrap"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VueJsonPretty from "vue-json-pretty";
import "vue-json-pretty/lib/styles.css";
import useClipboard from "vue-clipboard3";
import { ref, computed } from "vue";

export default {
  components: {
    VueJsonPretty,
  },
  setup() {
    const { toClipboard } = useClipboard();
    const input = ref("");
    const wrap = ref(null);

    const json = computed(() => {
      if (!input.value) {
        return "";
      }
      try {
        return JSON.parse(input.value);
      } catch (error) {
        return error;
      }
    });

    const getFormatJson = () => {
      const el = wrap.value.$el;
      return el.innerText;
    };

    const copy = async (txt) => {
      try {
        await toClipboard(txt);
        alert("复制成功到剪切板");
      } catch (e) {
        console.error(e);
      }
    };
    return {
      input,
      copy,
      json,
      wrap,
      getFormatJson,
    };
  },
  data() {
    return {};
  },
  computed: {},
  mounted() {},
  methods: {},
};
</script>

